/* –ü–æ–ª–Ω–æ–µ –ø–æ–∫—Ä—ã—Ç–∏–µ —ç–∫—Ä–∞–Ω–∞ */
html, body {
    height: 100vh;
    width: 100vw;
    margin: 0;
    padding: 0;
    font-family: "Arial", sans-serif;
    background: #000;
    color: #fff;
    display: flex;
    flex-direction: column;
    text-align: center;
    overflow: hidden;
}

/* –ì–ª–∞–≤–Ω—ã–π –∫–æ–Ω—Ç–µ–π–Ω–µ—Ä */
.app {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    height: 100vh;
    width: 100%;
}

/* –ö–æ–Ω—Ç–µ–Ω—Ç */
.scrollable-content {
    flex: 1;
    overflow-y: auto;
    -webkit-overflow-scrolling: touch;
    padding: 20px;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

/* –ù–∏–∂–Ω—è—è –ø–∞–Ω–µ–ª—å –Ω–∞–≤–∏–≥–∞—Ü–∏–∏ */
.bottom-nav {
    width: 230px; /* –§–∏–∫—Å–∏—Ä–æ–≤–∞–Ω–Ω–∞—è —à–∏—Ä–∏–Ω–∞ */
    height: 50px; /* –§–∏–∫—Å–∏—Ä–æ–≤–∞–Ω–Ω–∞—è –≤—ã—Å–æ—Ç–∞ */
    background: #141414;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 5px;
    position: fixed;
    bottom: 20px;
    left: 50%;
    transform: translateX(-50%); /* üî• –¶–µ–Ω—Ç—Ä–∏—Ä—É–µ–º –ø–∞–Ω–µ–ª—å */
    border-radius: 60px;
    box-shadow: 0px -5px 10px rgba(0, 0, 0, 0.2);
}

/* –ê–∫—Ç–∏–≤–Ω—ã–π –∏–Ω–¥–∏–∫–∞—Ç–æ—Ä */
.active-indicator {
    position: absolute;
    bottom: 0;
    width: 33.33%;
    height: 100%;
    background: #2c2c2c;
    border-radius: 60px;
    transition: left 0.15s ease-in-out;
}

/* –ö–Ω–æ–ø–∫–∏ –º–µ–Ω—é */
.bottom-nav button {
    background: none;
    border: none;
    cursor: pointer;
    transition: all 0.3s ease-in-out;
    padding: 0;
    border-radius: 50%;
    width: 50px;
    height: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    z-index: 10;
}

/* –°—Ç–∏–ª–∏ –¥–ª—è –∏–∫–æ–Ω–æ–∫ */
.button-icon {
    width: 26px;
    height: 26px;
    filter: brightness(0.7);
    transition: filter 0.3s ease-in-out, transform 0.2s ease-in-out;
}

/* –í—ã–¥–µ–ª–µ–Ω–Ω–∞—è –∫–Ω–æ–ø–∫–∞ */
.bottom-nav button.active .button-icon {
    filter: brightness(1);
    transform: scale(1.1);
}